/**
 * LifeRouteContainer.js
 * The wrapper container for life
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import { change, reset } from 'redux-form';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';
import Loadable from 'react-loadable';

import { resetCompare } from '../../actions/compareActions';
import { getPromoGiveaways, resetInsuranceForm, FORMSTATES } from '../../actions/insuranceActions';
import { clearLifeData } from '../../actions/lifeActions';
import { isProduction } from '../../lib/tagFunctions';

// import StepProgressBar from '../../components/Templates/StepProgressBar/StepProgressBar';
import RefreshTimer from '../../components/Templates/RefreshTimer/RefreshTimer';

// import LifeMainContainer from './LifeMainContainer';
// import LifeChoosePlanContainer from './LifeChoosePlanContainer';
// import LifeDetailContainer from './LifeDetailContainer';

import { routeHandler } from '../../lib/helperFunctions';

const formName = 'life';
const detailFormName = 'life-detail';

const StepProgressBar = Loadable({
  loader: () => import(/* webpackChunkName: "StepProgressBar" */ '../../components/Templates/StepProgressBar/StepProgressBar'),
  loading: () => null,
  modules: ['StepProgressBar']
});

const LifeMainContainer = Loadable({
  loader: () => import(/* webpackChunkName: "LifeMainContainer" */ './LifeMainContainer'),
  loading: () => null,
  modules: ['LifeMainContainer']
});

const LifeChoosePlanContainer = Loadable({
  loader: () => import(/* webpackChunkName: "LifeChoosePlanContainer" */ './LifeChoosePlanContainer'),
  loading: () => null,
  modules: ['LifeChoosePlanContainer']
});

const LifeDetailContainer = Loadable({
  loader: () => import(/* webpackChunkName: "LifeDetailContainer" */ './LifeDetailContainer'),
  loading: () => null,
  modules: ['LifeDetailContainer']
});

class LifeRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const { formState, match, history } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(formName, formState, formIndex, match, history);
      if (routeResult && routeResult.formIndex !== null) {
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      formIndex: 0,
      isInitialLoad: true,
    };

    const {
      resetStoreForm,
      termLifePromoGiveAway,
      wholeLifePromoGiveAway,
      clearData,
      history,
    } = this.props;

    // history.replace(`/life${history.location.search}`);

    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('life');
    resetStoreForm('life-detail');

    if (termLifePromoGiveAway === undefined || wholeLifePromoGiveAway === undefined) {
      this.props.getPromoGiveaways();
    }

    this.setInitialFormState = this.setInitialFormState.bind(this);
    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderLifeForms = this.renderLifeForms.bind(this);
    this.changeUserDetails = this.changeUserDetails.bind(this);
  }

  componentDidMount() {
    // window.addEventListener('beforeunload', this.handleLeavePage);
    this.setInitialFormState();
    this.setState({
      // eslint-disable-next-line
      isMounted: true,
    });
  }

  componentDidUpdate(prevProps) {
    const { isInitialLoad } = this.state;
    const { userDetails } = this.props;

    if (isInitialLoad && _.isEqual(userDetails, prevProps.userDetails)) {
      this.changeUserDetails();
    }
  }

  componentWillUnmount() {
    const { clearData, resetStoreForm } = this.props;
    clearData();
    this.props.resetCompare();
    this.props.resetInsuranceForm();

    resetStoreForm('life');
    resetStoreForm('life-detail');
  }

  setInitialFormState() {
    const { changeForm } = this.props;

    changeForm(formName, 'birth_date', '01 Jan 1990');
    changeForm(formName, 'sum_insured', '500000');
    changeForm(formName, 'premium_term', '5 years');
    changeForm(formName, 'premium_whole', '20 years');

    this.changeUserDetails();
  }

  changeUserDetails() {
    const { isInitialLoad } = this.state;
    const { userDetails, changeForm } = this.props;

    if (userDetails) {
      const surname = userDetails && userDetails.surname ? userDetails.surname : '';
      const firstname = userDetails && userDetails.firstName ? ` ${userDetails.firstName}` : '';

      changeForm(detailFormName, 'email', userDetails.email);
      changeForm(detailFormName, 'name', `${surname}${firstname}`);
      changeForm(detailFormName, 'birth_date', userDetails.dateOfBirth);
      changeForm(detailFormName, 'mobile_number', userDetails.contact);

      if (isInitialLoad) {
        this.setState({
          isInitialLoad: false,
        });
      }
    }
  }

  renderStepProgress() {
    const { formIndex } = this.state;
    return (
      <StepProgressBar
        maxWidth="556px"
        formState={formIndex}
        inputList={[
          'Choose Plan',
          'Details',
        ]}
        form="life"
        stateList={[
          FORMSTATES.CHOOSEPLAN,
          FORMSTATES.DETAILFORM,
        ]}
        stateDescriptions={[
          { main: '' /* 'Choose your Plan', sub: 'Compare price plans from multiple companies' */ },
          { main: 'Fill in your details - Get $5 Credits' },
        ]}
        charged={false}
      />
    );
  }

  renderLifeForms() {
    const { formIndex } = this.state;

    const {
      userDetails,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);


    switch (formIndex) {
      case FORMSTATES.CHOOSEPLAN.index:
        return <LifeChoosePlanContainer isLoggedIn={isLoggedIn} />;
      case FORMSTATES.DETAILFORM.index:
        return (
          <LifeDetailContainer
            isLoggedIn={isLoggedIn}
            piggyCallback={this.changeUserDetails}
          />
        );
      default:
        return <LifeMainContainer isLoggedIn={isLoggedIn} />;
    }
  }

  render() {
    const {
      termLifePromoGiveAway,
      wholeLifePromoGiveAway,
      constants,
    } = this.props;

    if (!constants || termLifePromoGiveAway === undefined || wholeLifePromoGiveAway === undefined) {
      return null;
    }

    const meta = isProduction ? 'PolicyPal, insurance, insurance broker, life insurance, term insurance, critical illness insurance, compare insurance, term life, insurance policy, insurance quotes, term insurance plans, term policy, life insurance quotes, types of life insurance, best life insurance, term life insurance, life quotes, life insurance quotes, term insurance comparison, life insurance singapore, whole life insurance, whole life, term vs life insurance, what is life insurance, term life vs whole life, term insurance comparison' : '';
    return (
      <Page id="life">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Life Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Life Insurance" />
          <meta name="description" content="Browse products for life insurance." />
        </Helmet>
        {/* {
          formState !== FORMSTATES.MAIN ?
            <Prompt
              when={true}
              message="Are you sure you want to leave current page?"
            />
          :
            null
        } */}
        {this.renderStepProgress()}
        {this.renderLifeForms()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
      </Page>
    );
  }
}

LifeRouteContainer.defaultProps = {
  userDetails: null,
  termLifePromoGiveAway: undefined,
  wholeLifePromoGiveAway: undefined,
  constants: null,
};

LifeRouteContainer.propTypes = {
  clearData: PropTypes.func.isRequired,
  resetCompare: PropTypes.func.isRequired,
  getPromoGiveaways: PropTypes.func.isRequired,
  resetInsuranceForm: PropTypes.func.isRequired,
  changeForm: PropTypes.func.isRequired,
  resetStoreForm: PropTypes.func.isRequired,
  userDetails: PropTypes.object,
  termLifePromoGiveAway: PropTypes.object,
  wholeLifePromoGiveAway: PropTypes.object,
  constants: PropTypes.object,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  termLifePromoGiveAway: state.insurance.promoGiveaways.termLife,
  wholeLifePromoGiveAway: state.insurance.promoGiveaways.wholeLife,
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  userDetails: state.userDetails.data,
});

const mapDispatchToProps = dispatch => ({
  changeForm: (form, key, value) => (
    dispatch(change(form, key, value))
  ),
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  resetStoreForm: form => (
    dispatch(reset(form))
  ),
  getPromoGiveaways: () => {
    dispatch(getPromoGiveaways('termLife'));
    dispatch(getPromoGiveaways('wholeLife'));
  },
  resetCompare: () => {
    dispatch(resetCompare());
  },
  clearData: () => {
    dispatch(clearLifeData());
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(LifeRouteContainer));
